import * as Yup from "yup"
import translate from "../../config/yup"

export const initialValues = {
	tipo: "4",
	arquivo: [],
	titulo: "",
	descricao: "",
}

Yup.setLocale(translate)
export const validationSchema = () => {
	return Yup.object().shape({
		titulo: Yup.string().min(2).max(255).required(),
	})
}

export const camposForm = (props) => {
	return [
		[
			{
				config: { xs: 12, sm: 12, xl: 12 },
				type: "column",
				fields: [
					{ type: "label", label: "Informaes" },

					{
						family: "input",
						type: "file",
						label: "Arquivos",
						name: "arquivo",
						maxFiles: 200,
						config: {
							disabled: props.loading,
						},
					},
					{
						family: "input",
						type: "text",
						label: "Titulo",
						name: "titulo",
						config: {
							disabled: props.loading,
							max: 255,
						},
					},
				],
			},
			{
				type: "column",
				config: { xs: 12, sm: 12, xl: 12 },
				fields: [
					{
						family: "input",
						type: "textarea",
						label: "Descrico",
						name: "descricao",
						config: {
							disabled: props.loading,
							rows: 5,
						},
					},
				],
			},
		],
	]
}
