import React, { useEffect, useState } from "react"
import { Route, useHistory } from "react-router-dom"
import { connect } from "react-redux"

import * as actions from "../../store/actions/index"
import { formatFormData } from "../../forms/functions/form"
import * as arquivoAPI from "../../services/functions/arquivo"
import * as clienteAPI from "../../services/functions/cliente"
import * as denominacaoAPI from "../../services/functions/denominacao"
import ErrorBoundary from "../../components/ErrorBoundary"
import DetalhesArquivo from "../../components/DetalhesArquivo"
import dataToSelect from "../../functions/dataToSelect"
import {
  Edit,
  AddCircle,
  Delete,
  Sync,
  GetApp,
  AccountTree,
  InfoRounded,
} from "@material-ui/icons"
import DataTable from "../../components/Datatable"
import ModalForm from "../../components/ModalForm"
import ConfirmModal from "../../components/UI/ConfirmModal"
import {
  initialValues as initialValuesArquivo,
  validationSchema as validationSchemaArquivo,
  camposForm as camposFormArquivo,
} from "../../forms/forms/arquivo"
import {
  initialValues as initialValuesArquivoEditar,
  validationSchema as validationSchemaArquivoEditar,
  camposForm as camposFormArquivoEditar,
} from "../../forms/forms/arquivoEditar"
import {
  initialValues as initialValuesArquivos,
  validationSchema as validationSchemaArquivos,
  camposForm as camposFormArquivos,
} from "../../forms/forms/arquivos"
import estilos from "./estilos.module.css"

const Arquivos = (props) => {
  const { appState } = props

  const [arquivos, setArquivos] = useState([])
  const [arquivosC, setArquivosC] = useState([])
  const [arquivo, setArquivo] = useState([])
  const [clientes, setClientes] = useState([])
  const [denominacoes, setDenominacoes] = useState([])
  const [linhaSelecionada, setLinhaSelecionada] = useState(null)

  const history = useHistory()
  const location = window.location.pathname.split("/")

  const colunasTabela = [
    {
      id: "data",
      align: "left",
      numeric: false,
      date: true,
      disablePadding: false,
      label: "Data",
    },
    {
      id: "codigo_cliente",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Cdigo",
    },
    {
      id: "cliente",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Cliente",
    },
    {
      id: "denominacao",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Denominao",
    },
    {
      id: "titulo",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Titulo",
    },
    {
      id: "tamanho",
      align: "left",
      numeric: false,
      date: false,
      disablePadding: false,
      label: "Tamanho",
    },
  ]

  const opcoesTabela = [
    {
      label: "Sincronizar Dados",
      click: () => fetchArquivosHandler(),
      icon: <Sync />,
    },
    {
      label: "Novo",
      click: () => {
        history.push("/arquivos/novo")
      },
      icon: <AddCircle />,
    },
    {
      label: "Arquivos em massa para clientes",
      click: () => {
        history.push("/arquivos/novos")
      },
      icon: <AccountTree />,
    },
  ]

  const opcoesTabelaSelecionada = [
    {
      label: "Detalhes",
      click: () => {
        fetchArquivoHandler()
        history.push("/arquivos/detalhes")
      },
      icon: <InfoRounded />,
    },
    {
      label: "Editar",
      click: () => {
        fetchArquivoHandler()
        history.push("/arquivos/editar")
      },
      icon: <Edit />,
    },
    {
      label: "Salvar",
      click: () => downloadArquivoHandler(),
      icon: <GetApp />,
    },
    {
      label: "Excluir",
      click: () => history.push("/arquivos/excluir"),
      icon: <Delete />,
    },
  ]

  const formOptions = {
    denominacoes: dataToSelect(denominacoes, "id", "nome"),
    clientes: dataToSelect(clientes, "id", "nome"),
    tipos: [
      { id: "1", label: "Todos" },
      { id: "2", label: "Denominaes" },
      { id: "3", label: "Clientes" },
    ],
    loading: appState.loading,
  }

  const camposArquivo = camposFormArquivo({ ...formOptions })
  const camposArquivoEditar = camposFormArquivoEditar({ ...formOptions })
  const camposArquivos = camposFormArquivos({ ...formOptions })

  const tabLabel = [{ label: "Arquivo" }]

  const trocarIdParaNomeAddCodigoCliente = () => {
    if (!clientes.length > 0 || !arquivos.length > 0) {
      return
    }

    let arqsC = []

    arqsC = arquivos.map((arq) => {
      if (!arq.id_cliente) {
        return {
          ...arq,
          codigo_cliente: 0,
        }
      }

      const cli = clientes.find((c) => {
        if (c.id === arq.id_cliente) {
          return true
        }
      })

      return {
        ...arq,
        codigo_cliente: cli ? cli.codigo : "",
      }
    })

    arqsC = arqsC.map((arq) => {
      let cliente
      clientes.map((cli) => {
        if (cli.id === arq.id_cliente) {
          cliente = cli
        }
      })

      return {
        ...arq,
        cliente: cliente ? cliente.nome : arq.cliente === 0 ? "" : arq.cliente,
      }
    })

    arqsC = arqsC.map((arq) => {
      let denominacao
      denominacoes.map((deno) => {
        if (deno.id === arq.id_denominacao) {
          denominacao = deno
        }
      })

      return {
        ...arq,
        denominacao: denominacao
          ? denominacao.nome
          : arq.denominacao === 0
          ? ""
          : arq.denominacao,
      }
    })

    setArquivosC(arqsC)
  }

  const changeProgressHandler = (file) => {
    if (!file) {
      return
    }

    props.setFilesTransferList(file)
  }

  const successHandler = () => {
    setLinhaSelecionada(null)
    history.goBack()
    fetchArquivosHandler()
    trocarIdParaNomeAddCodigoCliente()
    appState.setSuccess()
  }

  const goBackHandler = () => {
    setLinhaSelecionada(null)
    history.goBack()
  }

  const fetchClientesHandler = async () => {
    const c = await clienteAPI.fetchAll({ appState })

    if (!c) {
      return
    }

    setClientes(c)
  }

  const fetchDenominacoesHandler = async () => {
    const d = await denominacaoAPI.fetchAll({ appState })

    if (!d) {
      return
    }

    setDenominacoes(d)
  }

  const fetchArquivosHandler = async () => {
    const arqs = await arquivoAPI.fetchAll({ appState })

    if (!arqs) {
      return
    }

    setArquivos(arqs)
  }

  const fetchArquivoHandler = async () => {
    const arq = await arquivoAPI.fetchOne({
      id: linhaSelecionada,
      appState,
    })

    setArquivo(arq)
  }

  const novoArquivoHandler = async (arquivo) => {
    arquivoAPI.create({
      appState,
      arquivo,
      changeProgressHandler,
    })

    goBackHandler()
  }

  const novosArquivosHandler = async (arquivos) => {
    arquivoAPI.createMoreThanOne({
      form: {
        ...arquivos,
        arquivo: [...arquivos.arquivo],
      },
      appState,
      changeProgressHandler,
    })

    goBackHandler()
  }

  const editArquivoHandler = async (arquivo) => {
    const res = await arquivoAPI.update({
      arquivo,
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const deleteArquivoHandler = async () => {
    const res = await arquivoAPI.del({
      id: linhaSelecionada,
      appState,
    })

    if (res) {
      successHandler()
    }
  }

  const downloadArquivoHandler = () => {
    arquivoAPI.download({
      id: linhaSelecionada,
      appState,
      changeProgressHandler,
    })
  }

  const onFormSubmit = async (values) => {
    let newValues = await formatFormData(values)

    if (location[2] === "novo") {
      novoArquivoHandler(newValues)
    } else {
      if (location[2] === "novos") {
        novosArquivosHandler(newValues)
      } else if (location[2] === "editar") {
        editArquivoHandler(newValues)
      }
    }
  }

  useEffect(() => {
    fetchArquivosHandler()
    fetchClientesHandler()
    fetchDenominacoesHandler()
  }, [])

  useEffect(() => {
    trocarIdParaNomeAddCodigoCliente()
  }, [arquivos, clientes, denominacoes])

  return (
    <ErrorBoundary>
      <div className={estilos.Container}>
        <DataTable
          caixaPesquisa={true}
          colunasKey={"id"}
          colunasTabela={colunasTabela}
          fetchedRows={arquivosC}
          linhasPorPaginaSelecionada={15}
          linhaSelecionada={linhaSelecionada}
          loading={appState.loading}
          opcoesTabela={opcoesTabela}
          opcoesTabelaSelecionada={opcoesTabelaSelecionada}
          tituloTabela={"Arquivos"}
          setLinhaSelecionada={setLinhaSelecionada}
        />
      </div>

      <Route path="/arquivos/excluir">
        <ConfirmModal
          action={"delete"}
          history={history}
          location={location}
          okButtonClick={deleteArquivoHandler}
        />
      </Route>

      <Route path="/arquivos/detalhes">
        <DetalhesArquivo
          history={history}
          loading={appState.loading}
          location={location}
          {...arquivo}
        />
      </Route>

      <Route path="/arquivos/novo">
        <ModalForm
          camposForm={camposArquivo}
          initialValues={initialValuesArquivo}
          history={history}
          loading={appState.loading}
          location={location}
          onFormSubmit={onFormSubmit}
          tabLabel={tabLabel}
          validationSchema={validationSchemaArquivo}
        />
      </Route>

      <Route path="/arquivos/novos">
        <ModalForm
          camposForm={camposArquivos}
          initialValues={initialValuesArquivos}
          history={history}
          loading={appState.loading}
          location={location}
          onFormSubmit={onFormSubmit}
          tabLabel={tabLabel}
          validationSchema={validationSchemaArquivos}
        />
      </Route>

      <Route path="/arquivos/editar">
        <ModalForm
          camposForm={camposArquivoEditar}
          initialValues={arquivo ? arquivo : initialValuesArquivoEditar}
          history={history}
          loading={appState.loading}
          location={location}
          onFormSubmit={onFormSubmit}
          tabLabel={tabLabel}
          validationSchema={validationSchemaArquivoEditar}
        />
      </Route>
    </ErrorBoundary>
  )
}

const mapStateToProps = (state) => {
  return {}
}

const mapDispatchToProps = (dispatch) => {
  return {
    setFilesTransferList: (file) =>
      dispatch(actions.setFilesTransferList(file)),
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(Arquivos)
