import * as Yup from "yup"
import translate from "../../config/yup"

export const initialValues = {
	tipo: 0,
	id_denominacao: 0,
	id_cliente: "",
	arquivo: [],
	titulo: "",
	descricao: "",
}

Yup.setLocale(translate)
export const validationSchema = () => {
	return Yup.object().shape({
		tipo: Yup.number().required(),
		titulo: Yup.string().min(2).max(255).required(),
	})
}

export const camposForm = (props) => {
	return [
		[
			{
				type: "column",
				config: { xs: 12, sm: 12, xl: 12 },
				fields: [
					{
						family: "input",
						type: "rule",
						field: "tipo",
						becomes: "1",
						set: "id_cliente",
						to: "",
					},
					{
						family: "input",
						type: "rule",
						field: "tipo",
						becomes: "1",
						set: "id_denominacao",
						to: "",
					},
					{
						family: "input",
						type: "rule",
						field: "tipo",
						becomes: "3",
						set: "id_denominacao",
						to: "",
					},
					{
						family: "input",
						type: "rule",
						field: "tipo",
						becomes: "2",
						set: "id_cliente",
						to: "",
					},

					{ type: "label", label: "Informaes" },
					{
						family: "input",
						type: "select",
						label: "Tipo",
						name: "tipo",
						config: {
							disabled: props.loading,
						},
						options: props.tipos,
					},
					{
						family: "input",
						type: "select",
						label: "Denominaes",
						name: "id_denominacao",
						config: {
							disabled: props.loading,
						},
						options: props.denominacoes,
					},
					{
						family: "input",
						type: "autocomplete",
						label: "Cliente",
						name: "id_cliente",
						config: {
							disabled: props.loading,
						},
						options: props.clientes,
					},
					{
						family: "input",
						type: "text",
						label: "Titulo",
						name: "titulo",
						config: {
							disabled: props.loading,
							max: 255,
						},
					},
				],
			},
			{
				type: "column",
				config: { xs: 12, sm: 12, xl: 12 },
				fields: [
					{
						family: "input",
						type: "textarea",
						label: "Descrico",
						name: "descricao",
						config: {
							disabled: props.loading,
							rows: 5,
						},
					},
				],
			},
		],
	]
}
