import React, { useEffect, useState } from "react"
// import { dataBanco } from "../../shared/utility"

import Button from "@material-ui/core/Button"
import Modal from "../UI/Modal"
import classes from "./estilos.module.css"

const DetalhesArquivo = (props) => {
	const [isOpen, setIsOpen] = useState(true)

	const {
		data,
		titulo,
		file,
		tamanho,
		descricao,
		loading,
		location,
		history,
	} = props

	useEffect(() => {
		setIsOpen(location[1] ? true : false)
	}, [location])

	useEffect(() => {
		let newLoc = location
		if (!isOpen) {
			newLoc.pop()
			history.push(newLoc.join("/"))
		}
	}, [isOpen, location, history])

	return (
		<React.Fragment>
			{data || titulo || file || tamanho || descricao ? (
				<Modal isModalOpen={isOpen} setIsModalOpen={setIsOpen}>
					<div className={classes.Content}>
						<h3>Data</h3>
						{/* {loading ? "carregando..." : dataBanco(data)} */}
						<h3>Titulo</h3>
						{loading ? "carregando..." : titulo}
						<h3>Arquivo</h3>
						{loading ? "carregando..." : file}
						<h3>Tamanho</h3>
						{loading ? "carregando..." : tamanho + " KB"}
						<h3>Descrio</h3>
						<div className={classes.Descricao}>
							{loading ? "carregando..." : descricao.split("/n").join("\n")}
						</div>
					</div>
					<div className={classes.Footer}>
						<Button variant="contained" onClick={() => setIsOpen(false)}>
							Cancelar
						</Button>
					</div>
				</Modal>
			) : null}
		</React.Fragment>
	)
}

export default DetalhesArquivo
