import * as denominacaoAPI from "../api/denominacao"

export const fetchAll = async (actions) => {
  actions.appState.startLoading()

  try {
    const res = await denominacaoAPI.fetchAll()

    actions.appState.stopLoading()

    return res
  } catch (err) {
    actions.appState.stopLoading()
    actions.appState.setError(err)
  }

  return
}

export const fetchOne = async (actions) => {
  actions.appState.startLoading()

  try {
    const res = await denominacaoAPI.fetchOne({
      id: actions.id,
    })

    actions.appState.stopLoading()

    return res
  } catch (err) {
    actions.appState.stopLoading()
    actions.appState.setError(err)
  }

  return
}

export const create = async (actions) => {
  actions.appState.startLoading()

  try {
    await denominacaoAPI.create({
      denominacao: actions.denominacao,
    })

    actions.appState.stopLoading()

    return true
  } catch (err) {
    actions.appState.stopLoading()
    actions.appState.setError(err)
  }

  return
}

