import axios from "../../config/axios"
import { getUserToken } from "../functions/auth"
import isGraphQLError from "../functions/isGraphQLError"

export const fetchAll = async () => {
  const graphqlQuery = `
			{
				denominacoes {
					id
					nome
				}
			}
		`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.denominacoes
  } catch (err) {
    throw err
  }
}

export const fetchOne = async (dados) => {
  const graphqlQuery = `
		{
			denominacao(id: "${dados.id}") {
				id
				nome
			}
		}
		`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.denominacao
  } catch (err) {
    throw err
  }
}

export const create = async (dados) => {
  try {
    const graphqlQuery = `
			mutation {
				cadDenominacao(
					data: {
						nome: "${dados.denominacao.nome}"
					}
				)
			}
			`
    const request = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
        "Content-Type": "application/json",
      },
    }

    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return true
  } catch (err) {
    throw err
  }
}

