import * as Yup from "yup"
import translate from "../../config/yup"

export const initialValues = {
  tipo: 0,
  id_denominacao: 0,
  id_cliente: "",
  arquivo: [],
  titulo: "",
  descricao: "",
}

Yup.setLocale(translate)
export const validationSchema = () => {
  return Yup.object().shape({
    tipo: Yup.number().required(),
    titulo: Yup.string().min(2).max(255).required(),
  })
}

export const camposForm = (props) => {
  return [
    [
      {
        config: { xs: 12, sm: 12, xl: 12 },
        type: "column",
        fields: [
          {
            family: "rule",
            type: "rule",
            field: "tipo",
            becomes: "1",
            set: "id_cliente",
            to: "",
          },
          {
            family: "rule",
            type: "rule",
            field: "tipo",
            becomes: "1",
            set: "id_denominacao",
            to: "",
          },
          {
            family: "rule",
            type: "rule",
            field: "tipo",
            becomes: "3",
            set: "id_denominacao",
            to: "",
          },
          {
            family: "rule",
            type: "rule",
            field: "tipo",
            becomes: "2",
            set: "id_cliente",
            to: -1,
          },

          { type: "label", label: "Informaes" },
          {
            family: "input",
            type: "select",
            label: "Tipo",
            name: "tipo",
            config: {
              disabled: props.loading,
            },
            options: props.tipos,
          },
          {
            family: "input",
            type: "select",
            label: "Denominao",
            name: "id_denominacao",
            config: {
              disabled: props.loading,
            },
            options: props.denominacoes,
          },
          {
            family: "input",
            type: "autocomplete",
            label: "Cliente",
            name: "id_cliente",
            config: {
              disabled: props.loading,
            },
            options: props.clientes,
          },
          {
            family: "input",
            type: "file",
            label: "Arquivo",
            name: "arquivo",
            maxFiles: 1,
            config: {
              disabled: props.loading,
            },
          },
          {
            family: "input",
            type: "text",
            label: "Titulo",
            name: "titulo",
            config: {
              disabled: props.loading,
              max: 255,
            },
          },
        ],
      },
      {
        config: { xs: 12, sm: 12, xl: 12 },
        type: "column",
        fields: [
          {
            family: "input",
            type: "textarea",
            label: "Descrico",
            name: "descricao",
            config: {
              disabled: props.loading,
              rows: 5,
            },
          },
        ],
      },
    ],
  ]
}
