import serverIp from "../../config/server-ip"
import axios from "../../config/axios"
import { getUserToken } from "../functions/auth"
import isGraphQLError from "../functions/isGraphQLError"

export const fetchAll = async () => {
  const graphqlQuery = `
		{
			arquivos {
				id
				data
				id_cliente
        id_denominacao
        titulo
				file
				tamanho
				descricao
			}
		}
	`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.arquivos
  } catch (err) {
    throw err
  }
}

export const fetchOne = async (dados) => {
  const graphqlQuery = `
	{
		arquivo(id: "${dados.id}") {
			id
			id_cliente
			id_denominacao
			data
			descricao
			file
			tamanho
			titulo
			tipo
		}
	}
	`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.arquivo
  } catch (err) {
    throw err
  }
}

export const create = async (dados) => {
  try {
    const formData = new FormData()
    dados.arquivo.arquivo.map((arq) => {
      formData.append("arquivos", arq)
    })
    const request = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
      },
      onUploadProgress: (ProgressEvent) => {
        let percentCompleted = Math.floor(
          (ProgressEvent.loaded / ProgressEvent.total) * 100
        )

        dados.changeProgressHandler({
          id: dados.arquivo.arquivo[0].name,
          filename: dados.arquivo.arquivo[0].name,
          progress: percentCompleted,
        })
      },
    }

    const arquivo = await axios.put("/uploads", formData, request)

    const graphqlQuery = `
			mutation {
				novoArquivo (
					data: {
						tipo:  ${dados.arquivo.tipo}
						id_denominacao: ${
              !dados.arquivo.id_denominacao ? 0 : dados.arquivo.id_denominacao
            }
						id_cliente: ${!dados.arquivo.id_cliente ? 0 : dados.arquivo.id_cliente}
						file: "${arquivo.data.path[0].filename}"
						tamanho: ${arquivo.data.path[0].size}
						titulo: "${dados.arquivo.titulo}"
						descricao: "${dados.arquivo.descricao}"
					}
				)
			}
		`

    const requestForm = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
        "Content-Type": "application/json",
      },
    }
    const res = await axios.post(
      "/graphql",
      { query: graphqlQuery },
      requestForm
    )
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return true
  } catch (err) {
    dados.changeProgressHandler({
      id: dados.arquivo.arquivo[0].name,
      filename: dados.arquivo.arquivo[0].name,
      progress: "error",
    })

    throw err
  }
}

export const update = async (dados) => {
  const graphqlQuery = `
		mutation {
			editArquivo (
				data: {
					id:  ${dados.arquivo.id}
					tipo:  ${dados.arquivo.tipo}
					id_denominacao: ${
            !dados.arquivo.id_denominacao ? 0 : dados.arquivo.id_denominacao
          }
					id_cliente: ${!dados.arquivo.id_cliente ? 0 : dados.arquivo.id_cliente}
					titulo: "${dados.arquivo.titulo}"
					descricao: "${dados.arquivo.descricao}"
				}
			)
		}
	`
  const requestForm = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post(
      "/graphql",
      { query: graphqlQuery },
      requestForm
    )
    if (res.data.status === "error") {
      throw res
    }

    return true
  } catch (err) {
    throw err
  }
}

export const createMoreThanOne = async (dados) => {
  const filename = dados.form.titulo + " (upload em massa)"

  try {
    const formData = new FormData()
    dados.form.arquivo.map((arq) => {
      formData.append("arquivos", arq)
    })
    const request = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
      },
      onUploadProgress: (ProgressEvent) => {
        let percentCompleted = Math.floor(
          (ProgressEvent.loaded / ProgressEvent.total) * 100
        )

        dados.changeProgressHandler({
          id: filename,
          filename: filename,
          progress: percentCompleted,
        })
      },
    }

    const arquivos = await axios.put("/uploads", formData, request)

    let arquivosGraph = []
    arquivos.data.path.map(async (arq) => {
      arquivosGraph.push(`
				{
					file: "${arq.filename}"
					tamanho: ${arq.size}
					titulo: "${dados.form.titulo}"
					descricao: "${dados.form.descricao}"
				}
				`)
    })

    const graphqlQuery = `
			mutation {
				novosArquivosClientes (
					data: [
						${arquivosGraph}
					]
				)
			}
		`

    const requestForm = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
        "Content-Type": "application/json",
      },
    }
    const res = await axios.post(
      "/graphql",
      { query: graphqlQuery },
      requestForm
    )
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return true
  } catch (err) {
    dados.changeProgressHandler({
      id: filename,
      filename: filename,
      progress: "error",
    })

    throw err
  }
}

export const download = async (dados) => {
  const graphqlQuery = `
      {
        arquivo(id: "${dados.id}") {
          file
          titulo
        }
      }
    `
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  let file = ""
  let filename = ""

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)

    file = res.data.data.arquivo.file
    filename = file.split("-")

    if (typeof filename === "object") {
      filename.shift()
      filename = filename.join("-")
    }

    const reqUrl = serverIp + "/uploads/" + file + `?id=${dados.id}`
    const reqConfig = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
        "Content-Type": "application/json",
      },

      onDownloadProgress: (ProgressEvent) => {
        if (ProgressEvent.currentTarget.status === 404) {
          return
        }

        let percentCompleted = Math.floor(
          (ProgressEvent.loaded / ProgressEvent.total) * 100
        )

        dados.changeProgressHandler({
          id: file,
          filename: filename,
          progress: percentCompleted,
        })
      },
    }

    const response = await axios.get(reqUrl, reqConfig)

    const url = window.URL.createObjectURL(new Blob([response.data]))
    let link = document.createElement("a")
    link.href = url
    link.setAttribute("download", file)

    link.download = filename

    document.body.appendChild(link)
    link.click()
  } catch (err) {
    dados.changeProgressHandler({
      id: file,
      filename: filename,
      progress: "error",
    })

    throw err
  }
}

