import * as arquivoAPI from "../api/arquivo"

export const fetchAll = async (actions) => {
  actions.appState.startLoading()

  try {
    const res = await arquivoAPI.fetchAll()

    actions.appState.stopLoading()

    return res
  } catch (err) {
    actions.appState.stopLoading()
    actions.appState.setError(err)
  }

  return
}

export const fetchOne = async (actions) => {
  actions.appState.startLoading()

  try {
    const res = await arquivoAPI.fetchOne({
      id: actions.id,
    })

    actions.appState.stopLoading()

    return res
  } catch (err) {
    actions.appState.stopLoading()
    actions.appState.setError(err)
  }

  return
}

export const create = async (actions) => {
  try {
    await arquivoAPI.create({
      arquivo: actions.arquivo,
      changeProgressHandler: actions.changeProgressHandler,
    })

    return true
  } catch (err) {
    actions.appState.setError(err)
  }

  return
}

