import axios from "../../config/axios"
import { getUserToken } from "../functions/auth"
import isGraphQLError from "../functions/isGraphQLError"

export const fetchAll = async () => {
  const graphqlQuery = `
			{
				clientes {
					id
					codigo
					nome
					email_login
					id_denominacao
					cliente_web
					status
				}
			}
		`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.clientes
  } catch (err) {
    throw err
  }
}

export const fetchOne = async (dados) => {
  const graphqlQuery = `
		{
			cliente(id: "${dados.id}") {
				id
				codigo
				nome
				email_login
				id_denominacao
				cliente_web
				status
			}
		}
		`
  const request = {
    headers: {
      Authorization: "Bearer " + getUserToken(),
      "Content-Type": "application/json",
    },
  }

  try {
    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return res.data.data.cliente
  } catch (err) {
    throw err
  }
}

export const create = async (dados) => {
  try {
    const graphqlQuery = `
			mutation {
				cadCliente(
					data: {
						codigo: ${dados.cliente.codigo}
						nome: "${dados.cliente.nome}"
						email_login: "${dados.cliente.email_login}"
						id_denominacao: ${dados.cliente.id_denominacao}
						cliente_web: ${dados.cliente.cliente_web}
						status: ${dados.cliente.status}
					}
				)
			}
			`
    const request = {
      headers: {
        Authorization: "Bearer " + getUserToken(),
        "Content-Type": "application/json",
      },
    }

    const res = await axios.post("/graphql", { query: graphqlQuery }, request)
    if (isGraphQLError(res)) {
      throw res.data.errors[0]
    }

    return true
  } catch (err) {
    throw err
  }
}

